import React, { Component } from 'react'
import '../Home.css'

class Withdraw extends Component {
  constructor(props) {
    super(props)
    this.state = {
      note: '',
      recipient: '',
      errMsg: '',
      pending: false,
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleDonate = this.handleDonate.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleReq = this.handleReq.bind(this)
  }
  
  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  
  handleDonate() {
    const { REACT_APP_ETH_TORNADO_ADDRESS } = process.env
    this.setState({ recipient: REACT_APP_ETH_TORNADO_ADDRESS })
  }
  
  handleSubmit(e) {
    e.preventDefault()
    const { note, recipient } = this.state
    if (note.length > 0 && recipient.length > 0) {
      this.handleReq({ note: note.trim(), recipient: recipient.trim() })
    }
  }
  
  async handleReq({...args}) {
    const amt = {
      '0.1': '0x61ed2a493f82326973f15144a9D3eAdE41b5b27F',
      '1': '0xAb1DbFe3D1b974705d571d0926e02f31a863BB15',
      '10': '0x56fbEEFB4db1792Fe64C602305364708522d284f',
      '100': '0xc1B3F94a4661e139b88670855c530C73aEe438cf',
    }
    let currentAddress = amt[this.props.tokenAmt]
    console.log('currentAddress', currentAddress)
    await this.setState({ pending: true })
    try {
      console.log('args.note', args.note)
      console.log('args.recipient', args.recipient)
      console.log('currentAddress', currentAddress)
      await window._MakeWithdrawal(args.note, args.recipient, currentAddress)
    } catch(e) {
      this.setState({ errMsg: e.message })
    }
    await this.setState({ pending: false })
  }
  
  render() {
    const { recipient, note, errMsg, pending } = this.state
    return (
      <form onSubmit={this.handleSubmit} style={{ display: 'flex', flexDirection: 'column', width: '25vw', }}>
        <div style={{ padding: '0 1vw', fontWeight: 'bold' }}>Note</div>
        <textarea
          name={'note'}
          value={note}
          onChange={this.handleChange}
          className={'withdrawNote'}
          placeholder={'Enter your note here'}
          spellCheck={'false'}>
        </textarea>
        <div className={'withdrawRecipHeader'}>
          <div style={{ fontWeight: 'bold' }}>Recipient</div>
          <div style={{ color: '#1F33E8', cursor: 'pointer' }} onClick={this.handleDonate}>Donate</div>
        </div>
        <input
          type={'text'}
          name={'recipient'}
          value={recipient}
          onChange={this.handleChange}
          className={'withdrawInput'}
          placeholder={'address'}
          spellCheck={'false'}/>
        <input
          type={'submit'}
          value={'Withdraw'}
          className={'homeConnect'}
          style={{
            // width: '80px', // for mobile?
            width: '10vw',
            margin: '5vh 0',
            alignSelf: 'center',
            color: '#D7DADC',
            border: '1px solid #1F33E8',
            padding: '1vw 1vh',
            textAlign: 'center',
            borderRadius: '2px',
            backgroundColor: 'transparent',
            cursor: 'pointer',
          }}/>
        {pending ?
          <div className={'loadC'} style={{ margin: '0 auto' }}>
            <div id={'lodr'}></div>
            <div id={'inC'}></div>
          </div>
          :
          null
        }
        {errMsg !== '' ?
          <div style={{ margin: '0 auto' }}>{errMsg}</div>
          :
          null
        }
      </form>
    )
  }
}

export default Withdraw
