import React, { Component } from 'react'
import { nanoid } from 'nanoid'
import { formatDistance } from 'date-fns'
import ETHTornado from '../../ethereum/ETHTornado'
import './Home.css'

class Stats extends Component {
  constructor(props) {
    super(props)
    this.state = {
      usrIP: props.usrIP,
      uCount: props.uCount,
      arrTx: [],
    }
    this.handleEvents = this.handleEvents.bind(this)
  }
  
  async componentDidMount () {
    await this.handleEvents()
  }
  
  async handleEvents() {
    const amt = {
      '0.1': '0x61ed2a493f82326973f15144a9D3eAdE41b5b27F',
      '1': '0xAb1DbFe3D1b974705d571d0926e02f31a863BB15',
      '10': '0x56fbEEFB4db1792Fe64C602305364708522d284f',
      '100': '0xc1B3F94a4661e139b88670855c530C73aEe438cf',
    }
    let currentAddress = amt[this.props.tokenAmt]
    let events = await ETHTornado(currentAddress).getPastEvents('Deposit', { fromBlock: 0, toBlock: 'latest' })
    let _eventsLength = await events.length
    events = await events.length >= 18 ? events.reverse().slice(0, 18) : events.reverse()
    let filtered = await events.map(x => {
      let txTime = formatDistance(parseInt(x.returnValues.timestamp + '000'), Date.now())
      if (txTime.indexOf('about ') !== -1) {
        txTime = txTime.split('about ')[1]
      }
      return {
        txNo: parseInt(x.returnValues['1']) + 1,
        txTime: txTime + ' ago',
      }
    })
    await this.setState({ arrTx: filtered, uCount: _eventsLength })
  }
  
  componentDidUpdate (pP, pSt, snapshot) {
    if (pP.tokenAmt !== this.props.tokenAmt) {
      this.handleEvents()
    }
  }
  
  render() {
    const { state: { usrIP, arrTx, uCount }, props: { token, tokenAmt } } = this
    return (
      <div className={'statistics'}>
        <div style={{ display: 'flex' }}>
          <div className={'statsHeader'}>Statistics</div>
          <div className={'statsTokAmt'}>{tokenAmt} {token}</div>
        </div>
        <div className={'statsSubHeader'}>Anonymity set</div>
        <div style={{ padding: '0 1vw' }}>{uCount} equal user deposits</div>
        <div className={'statsSubHeader'}>latest deposits</div>
        <div className={'recentTxns'}>
          {arrTx.map((x, i) => (
            <div key={nanoid(4)} style={{ padding: '0 1vw', backgroundColor: `${i % 2 === 1 ? '#1a1a1a' : '#212121'}` }}>
              <span style={{ fontWeight: 'bold', fontSize: '14px' }}>
                {x.txNo}. {x.txTime}
              </span>
            </div>
          ))}
        </div>
        <div className={'statsIP'}>
          <div>Your IP</div>
          <div>{usrIP}</div>
        </div>
      </div>
    )
  }
}

export default Stats
